import Vue from 'vue'
import Router from 'vue-router'
import ProgramDetailView from "@/views/ProgramDetailView.vue";
import ProgramListView from "@/views/ProgramListView.vue";
import ArticleListView from "@/views/ArticleListView.vue";
import ArticleDetailView from "@/views/ArticleDetailView.vue";
import ContactMeView from "@/views/ContactMeView.vue";
import HomeView from "@/views/HomeView.vue";

Vue.use(Router)
export default new Router({
    routes: [
        {
            path: "/",
            name: "homeView",
            component: HomeView,
        },
        {
            path: "/program-list-view/",
            name: "programListView",
            component: ProgramListView,
        },
        {
            path: "/program-detail-view/:id",
            props: true,
            name: "programDetailView",
            component: ProgramDetailView,
        },
        {
            path: "/article-list-view/",
            name: "articleListView",
            component: ArticleListView,
        },
        {
            path: "/article-detail-view/:id",
            props: true,
            name: "articleDetailView",
            component: ArticleDetailView,
        },
        {
            path: "/contact-me-view/",
            name: "contactMeView",
            component: ContactMeView,
        },
    ],
})

