<template>
  <div>
    <b-card-group deck>
      <b-card
        v-for="video in videos"
        :key="video.id"
        :title="video.channel + video.name"
        :img-src="video.coverUrl"
        img-alt="Image"
        img-top
      >
        <b-button
          :to="{ name: 'programDetailView', params: { id: video.videoId } }"
          variant="primary"
          >详情</b-button
        >
      </b-card>
    </b-card-group>
  </div>
</template>

<script>
import Vue from "vue";
import VideoList from "../assets/VideoList";
import WebpPlugun from "vue-webp-plugin";
Vue.use(WebpPlugun);

export default {
  name: "ProgramListCards",
  props: ["selectedChannel", "searchStr"],
  data() {
    return { videos: VideoList };
  },
  watch: {
    searchStr: function (newVal) {
      this.videos = VideoList.filter(
        (video) => video.name.includes(newVal) || video.channel.includes(newVal)
      );
    },
    selectedChannel: function (newVal) {
      if (newVal.length == 0) {
        this.videos = VideoList;
      } else {
        this.videos = VideoList.filter((video) =>
          newVal.includes(video.channel)
        );
      }
    },
  },
};
</script>

