<template>
  <div class="container">
    <form>
      <label>姓名</label>
      <input
        type="text"
        v-on:keyup="updateOutputUrl"
        v-model="name"
        placeholder="你的名字"
      />
      <label>主题</label>
      <input
        type="text"
        v-on:keyup="updateOutputUrl"
        v-model="subject"
        placeholder="邮件主题"
      />
      <label>留言</label>
      <textarea
        v-on:keyup="updateOutputUrl"
        v-model="message"
        cols="30"
        rows="5"
        placeholder="留言"
      >
      </textarea>

      <b-button variant="primary" v-bind:href="outputUrl">发送</b-button>
    </form>
  </div>
</template>
<script>
// import $ from "jquery";

export default {
  name: "ContactMeForm",
  data() {
    return {
      name: "",
      subject: "",
      message: "",
      outputUrl: "",
    };
  },
  methods: {
    // mailto:you@example.com?subject=Hey%20there&cc=somebody@example.com&bcc=somebody.else@example.com&body=Hey%20there,%20how%20have%20you%20been?
    updateOutputUrl() {
      this.outputUrl = "mailto:stephanie.programmer@gmail.com";
      this.outputUrl =
        this.outputUrl +
        "?subject=个人网站from " +
        this.name +
        ": " +
        this.subject;
      this.outputUrl = this.outputUrl + "&body=" + this.message;
    },
  },
};
</script>

