import VideoList from "./VideoList.json"
import ArticleList from "./ArticleList.json"


export function getVideoDetailById(id) {
    const videoInfo = VideoList.filter(video => video.videoId == id)[0];
    if (videoInfo.haveCode) {
        const code = require("raw-loader!../../public/codes/" + videoInfo.name + ".txt");
        return {
            videoUrl: videoInfo.videoUrl,
            haveCode: true,
            language: "python",
            code: code.default,
        };
    } else {
        return {
            videoUrl: videoInfo.videoUrl,
            haveCode: false,
            language: "",
            code: "",
        };
    }
}

export function getVideoTypes() {
    const types = VideoList.map(video => video.channel);
    return [...new Set(types)];
}

export function getArticleTypes() {
    const types = ArticleList.map(article => article.channel);
    return [...new Set(types)];
}
