<template>
  <div v-html="articleHtml"></div>
</template>

<script>
export default {
  name: "ArticleDetailView",
  props: ["id"],
  created() {
    const axios = require("axios").default;

    axios
      .get(`../../articles/article-${this.id}.html`)
      .then((result) => {
        this.articleHtml = result.data;
        this.articleHtml = this.articleHtml.replace(
          'class="Post-Title"',
          'style="margin: 5% 0 5% 0;"'
        );
        this.articleHtml = this.articleHtml.replace(
          'class="RichText ztext Post-RichText"',
          'style="font-size: large;"'
        );
      })
      .catch((error) => {
        console.error(error);
      });
  },
  data() {
    return { articleHtml: "" };
  },
};
</script>

