<template>
  <div>
    <b-card-group deck>
      <b-card
        v-for="article in articles"
        :key="article.id"
        :title="article.channel + article.name"
        :img-src="article.coverUrl"
        img-alt="Image"
        img-top
      >
        <b-button
          :to="{ name: 'articleDetailView', params: { id: article.articleId } }"
          variant="primary"
          >详情</b-button
        >
      </b-card>
    </b-card-group>
  </div>
</template>

<script>
import Vue from "vue";
import ArticleList from "../assets/ArticleList";
import WebpPlugun from "vue-webp-plugin";
Vue.use(WebpPlugun);

export default {
  name: "ArticleListCards",
  props: ["selectedChannel", "searchStr"],
  data() {
    return { articles: ArticleList };
  },
  watch: {
    searchStr: function (newVal) {
      this.articles = ArticleList.filter(
        (article) =>
          article.name.includes(newVal) || article.channel.includes(newVal)
      );
    },
    selectedChannel: function (newVal) {
      if (newVal.length == 0) {
        this.articles = ArticleList;
      } else {
        this.articles = ArticleList.filter((article) =>
          newVal.includes(article.channel)
        );
      }
    },
  },
};
</script>

